"use client";

import { useState, useEffect } from "react";
import Link from "next/link";

export default function Kalender() {
  const [modules, setModules] = useState([]);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");

  // Fetch existing modules
  useEffect(() => {
    async function fetchModules() {
      try {
        const res = await fetch("/api/modules");
        const data = await res.json();
        if (data.success) {
          setModules(data.modules);
        }
      } catch (error) {
        console.error("Error fetching modules:", error);
      }
    }
    fetchModules();
  }, []);

  // Handle form submission to create a new module
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const res = await fetch("/api/modules", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ title, description }),
      });
      const data = await res.json();
      if (data.success) {
        setModules([...modules, { _id: data.id, title, description }]);
        setTitle("");
        setDescription("");
      }
    } catch (error) {
      console.error("Error creating module:", error);
    }
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">Module Ãœbersicht</h1>

      {/* Form to create a new module */}
      <div className="mb-8">
        <h2 className="text-xl font-semibold mb-2">Neues Modul erstellen</h2>
        <form onSubmit={handleSubmit} className="flex flex-col gap-4 max-w-md">
          <div>
            <label htmlFor="title" className="block text-sm font-medium">
              Titel:
            </label>
            <input
              type="text"
              id="title"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              className="mt-1 p-2 border rounded w-full"
              required
            />
          </div>
          <div>
            <label htmlFor="description" className="block text-sm font-medium">
              Beschreibung:
            </label>
            <textarea
              id="description"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              className="mt-1 p-2 border rounded w-full"
              rows="3"
              required
            />
          </div>
          <button type="submit" className="bg-blue-500 text-white p-2 rounded">
            Modul erstellen
          </button>
        </form>
      </div>

      {/* Grid of modules */}
      <div>
        <h2 className="text-xl font-semibold mb-2">Vorhandene Module</h2>
        {modules.length > 0 ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-4">
            {modules.map((module) => (
              <Link href={`/kalender/${module._id}`} key={module._id}>
                <div className="p-4 border rounded shadow hover:bg-gray-100 cursor-pointer">
                  <h3 className="text-lg font-medium">{module.title}</h3>
                  <p className="text-sm text-gray-600">{module.description}</p>
                </div>
              </Link>
            ))}
          </div>
        ) : (
          <p>Keine Module vorhanden.</p>
        )}
      </div>
    </div>
  );
}
